import React from 'react';
import Movies from './Movies';

function Alert(){
    window.confirm('Redirecting..');
    window.open("https://media.netflix.com/en/");
}

function Header() {
    return (
        <div className='main-bg'>
        <div className='main-heading' id='heading-box'>
            <h1>BLOCK BUSTER <span>Movies</span></h1>
            <p className='details'>NetFlix Presenting you all new Awesome Movies &amp; Series on your Touch!</p>
            <div class="header-btns">
                <a href="/Movies" target="_top">
                <button onClick={Alert} className='header-btn'>Browse &nbsp;&#9655;</button></a>
                <a className='close'></a>
            </div>
        </div>
        </div>
    );
}

export default Header;
